/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-writers-block-block-generate-suggestion {
  border: 1px solid #3b2479;
  padding: 0; }

.wp-block-writers-block-block-generate-suggestion .disclaimer, .wp-block-writers-block-block-generate-suggestion .components-base-control__label {
  width: 100%;
  background-color: #3b2479;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 8px;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: small; }

.wp-block-writers-block-block-generate-suggestion .content {
  padding: 4px; }

.wp-block-writers-block-block-generate-suggestion .components-text-control__input {
  margin: 10px;
  width: 80%; }

.wp-block-writers-block-block-generate-suggestion .components-button.is-primary {
  margin: 10px; }
